	module.exports = {"data":{"allDataJson":{"edges":[{"node":{"bmjsiteHead":[{"id":"34866726","accountId":"5351042","webPropertyId":"UA-5351042-34","name":"bmjopen.bmj.com - Reporting","websiteUrl":"http://bmjopen.bmj.com"}],"bmjsiteData":[{"pagePath":"/content/5/2/e006784","pageTitle":"The combined perceptions of people with stroke and their carers regarding rehabilitation needs 1 year after stroke: a mixed methods study | BMJ Open","pageviews":"994"},{"pagePath":"/content/8/3/e018895","pageTitle":"Maternal caffeine intake during pregnancy and childhood growth and overweight: results from a large Norwegian prospective observational cohort study | BMJ Open","pageviews":"967"},{"pagePath":"/content/4/9/e005055","pageTitle":"The role of hospital managers in quality and patient safety: a systematic review | BMJ Open","pageviews":"857"},{"pagePath":"/content/8/2/e018846","pageTitle":"Assessing the nutrient intake of a low-carbohydrate, high-fat (LCHF) diet: a hypothetical case study design | BMJ Open","pageviews":"717"},{"pagePath":"/content/5/11/e008052.full","pageTitle":"Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share | BMJ Open","pageviews":"672"},{"pagePath":"/content/8/4/e019399","pageTitle":"Support for midlife anxiety diagnosis as an independent risk factor for dementia: a systematic review | BMJ Open","pageviews":"622"},{"pagePath":"/content/6/6/e010401","pageTitle":"Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review | BMJ Open","pageviews":"548"},{"pagePath":"/content/7/5/e015233","pageTitle":"Long-term antibiotics for prevention of recurrent urinary tract infection in older adults: systematic review and meta-analysis of randomised trials | BMJ Open","pageviews":"473"},{"pagePath":"/content/8/3/e019438","pageTitle":"Effect of pasta in the context of low-glycaemic index dietary patterns on body weight and markers of adiposity: a systematic review and meta-analysis of randomised controlled trials in adults | BMJ Open","pageviews":"440"},{"pagePath":"/content/7/10/e016089","pageTitle":"Do emotions related to alcohol consumption differ by alcohol type? An international cross-sectional survey of emotions associated with alcohol consumption and influence on drink choice in different settings | BMJ Open","pageviews":"397"}]}}]}},"pathContext":{"group":[{"node":{"id":"dc25f630-f3f2-5330-ba0c-ec9b98107cb7","slug":"most-read-articles-in-february","title":"Most read articles in February","excerpt":"<p>The most read article in February was Kripke et al&#8216;s study of the association between hypnotics and mortality, which was only published on February 27th (attracting almost 17,000 views in just a few days) and received widespread press attention. The second most read was Townsend and Pitchford&#8217;s study comparing the effect of different weaning styles on [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/03/06/most-read-articles-in-february/\">Read More&#8230;</a></p>\n","status":"publish","date":"March 06, 2012","template":"","format":"standard"}},{"node":{"id":"1468bb61-8480-5006-931b-091b9d5035c0","slug":"bmj-opens-first-birthday","title":"BMJ Open&#8217;s first birthday","excerpt":"<p>  It is a year today that BMJ Open published its first papers: prompting donuts all round! We have now published over 230 open access research articles, covering niche topics and major public health issues alike. Several articles have received plenty of press coverage. Some have prompted considerable national debate, such as this paper suggesting that there [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/02/23/bmj-opens-first-birthday/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 23, 2012","template":"","format":"standard"}},{"node":{"id":"b54e01df-2a41-53b2-b133-0c5d2c26bad9","slug":"most-read-articles-in-january","title":"Most read articles in January","excerpt":"<p>The most read article in January was Bito et al&#8216;s randomised controlled trial to evaluate the efficacy of wrap therapy in treating pressure ulcers. Both Crawley et al&#8216;s paper on chronic fatigue syndrome amongst school-aged children and Margel and Fleshner&#8217;s ecological study of prostate cancer and the oral contraceptive pill remained highly popular. Rank Author(s) [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/02/08/most-read-articles-in-january/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 08, 2012","template":"","format":"standard"}},{"node":{"id":"f7c7a29d-ff06-5a31-aa94-432a49701d6f","slug":"high-levels-of-burnout-among-uk-family-doctors-especially-in-group-practice","title":"High levels of burnout among UK family doctors, especially in group practice","excerpt":"<p>Levels of burnout in UK general practice are high, suggests a study of general practitioners (GPs) in one area of South East England, published in BMJ Open. Male doctors, those who work in group practices, and those who repeatedly see the same patients seem to be at significantly greater risk, the research shows, prompting the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/02/02/high-levels-of-burnout-among-uk-family-doctors-especially-in-group-practice/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 02, 2012","template":"","format":"standard"}},{"node":{"id":"9a29fbc8-72b2-5e12-84a0-c7f73efda3d6","slug":"most-read-articles-in-december","title":"Most read articles in December","excerpt":"<p>The most read article in December was Crawley et al&#8216;s analysis of Chronic Fatigue System and school absence which was covered by several media outlets including the Telegraph, BBC News, The Guardian and the Nursing Times. Margel and Fleshner&#8217;s ecological analysis of oral contraceptive pill use and prostate cancer remained popular and McGlade et al&#8217;s [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2012/01/10/most-read-articles-in-december/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 10, 2012","template":"","format":"standard"}},{"node":{"id":"44a7c33c-72ee-553d-adcc-ff9141dc8fb4","slug":"most-read-articles-in-november","title":"Most read articles in November","excerpt":"<p>The most read BMJ Open article in November was Margel and Fleshner&#8217;s ecological study of the association between oral contraceptive use and prostate cancer. The paper received lots of press attention, with articles in the UK, India, Canada and the US. Brett et al&#8216;s paper on effective communication with parents of pre-term infants was also popular [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/12/07/most-read-articles-in-november/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 07, 2011","template":"","format":"standard"}},{"node":{"id":"6901c404-3f9b-510c-80b5-1c062349b952","slug":"most-read-articles-in-october","title":"Most read articles in October","excerpt":"<p>The most read article in October was Overgaard et al’s comparison of birth outcomes in freestanding midwifery units and obstetric units in Denmark. The article by Holden et al on the costs of prescribing analogue insulin remains highly viewed in second place and McGlade et al’s study of the regional variation in organ donation in the UK received coverage [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/11/07/most-read-articles-in-october/\">Read More&#8230;</a></p>\n","status":"publish","date":"November 07, 2011","template":"","format":"standard"}},{"node":{"id":"b3099e54-819a-5c21-974d-90e9717ac49e","slug":"exploring-open-access-in-higher-education","title":"Exploring open access in higher education","excerpt":"<p>  BMJ Open will be taking part in the The Guardian&#8217;s Higher Education Network&#8217;s live blog/debate tomorrow on Exploring open access in higher education. Discussion kicks off at noon, BST and will run until 2 pm. There is a large panel, drawn from publishing, academia, industry and policy. The event will &#8216;consider the various ways in which higher [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/10/27/exploring-open-access-in-higher-education/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 27, 2011","template":"","format":"standard"}},{"node":{"id":"605a45c0-1175-5ba3-b2ac-b946409d9a6a","slug":"most-popular-articles-in-september","title":"Most popular articles in September","excerpt":"<p>September&#8217;s most-accessed articles are topped by the article by Holden et al. on costs to the UK NHS of prescribing analogue insulin. This article prompted some strongly worded responses on our site, and you can read the responses here. The BBC, among others, also covered this research (view their report here). Click on the titles to view the articles [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/10/25/most-popular-articles-in-september/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 25, 2011","template":"","format":"standard"}},{"node":{"id":"b9dab477-6aea-5a86-8211-0efacf08c63c","slug":"open-access-week-is-here","title":"Open Access Week is here","excerpt":"<p>  BMJ Open is proudly sponsoring Open Access Week 2011. A global event, now in its fifth year, Open Access Week promotes open access as a new norm in scholarship and research. The website currently lists 119 upcoming events so have a look to see what is happening near you or browse their back catalogue [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/10/25/open-access-week-is-here/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 25, 2011","template":"","format":"standard"}}],"pathPrefix":"","first":false,"last":false,"index":24,"pageCount":27,"additionalContext":{}}}

